import { TextPoint, EdgePoint } from '@ali/4ever-cangjie';

export interface NewlineGuideDataProps {
  key?: string;
  triggered?: boolean;
  // 记录插入位置，用于搜索功能
  point?: TextPoint | EdgePoint;
  query?: string;
  triggerText?: string;
  showMenu?: boolean;
}

export const NEWLINE_REMOVE_TRIGGER_TEXT = Symbol('NEWLINE_REMOVE_TRIGGER_TEXT');
export const NEWLINE_REMOVE_DATA = Symbol('NEWLINE_REMOVE_DATA');
export const NEWLINE_SET_DATA = Symbol('NEWLINE_SET_DATA');
export const MOUNT_PLUS_TOOLBAR = Symbol('MOUNT_PLUS_TOOLBAR');
export const MOUNT_SLASH_TOOLBAR = Symbol('MOUNT_SLASH_TOOLBAR');

export function removeNewlineTriggerText(needRemoveEmptyParagraph: boolean) {
  return {
    type: NEWLINE_REMOVE_TRIGGER_TEXT,
    payload: {
      needRemoveEmptyParagraph,
    }
  };
}

export function removeNewlineData() {
  return {
    type: NEWLINE_REMOVE_DATA,
  };
}

export function setNewlineData(payload: NewlineGuideDataProps) {
  return {
    type: NEWLINE_SET_DATA,
    payload,
  };
}

export function mountInsertMenuFromLeftToolbar() {
  return {
    type: MOUNT_PLUS_TOOLBAR,
  };
}

export function mountInsertMenu() {
  return {
    type: MOUNT_SLASH_TOOLBAR,
  };
}

